const apiMotor = 'http://localhost:3000/engine';
let idStart;
let idAnim;
let time;
//  const buttonRace = document.querySelector('.input-button__race');
export const apiStartMotor = async (id) => {
    try {
        const response = await fetch(`${apiMotor}?id=${id}&status=started`, { method: 'PATCH' });
        const data = response.json();
        console.log(data);
        return data;
    }
    catch (error) {
        console.error('Error starting motor:', error.message);
        throw error;
    }
};
export async function apiStopMotor(id) {
    try {
        const response = await fetch(`${apiMotor}?id=${id}&status=stopped`, { method: 'PATCH' });
        await response.json();
    }
    catch (error) {
        console.error('Error stopping motor:', error.message);
    }
}
export async function apiDriveMotor(id) {
    try {
        const response = await fetch(`${apiMotor}?id=${id}&status=drive`, { method: 'PATCH' });
        if (response.status !== 200) {
            return { success: false };
        }
        const arrDrive = await response.json();
        console.log(arrDrive);
        return arrDrive;
    }
    catch (error) {
        console.error('Error driving motor:', error.message);
        return { success: false };
    }
}
function animation(car, distance, duration) {
    let startTime = 0;
    idAnim = idStart;
    console.log(idAnim);
    function step(timestamp) {
        if (!startTime) {
            startTime = timestamp;
        }
        const progress = (timestamp - startTime) / duration;
        const translate = progress * distance;
        car.style.transform = `translateX(${translate}px)`;
        if (progress < 1) {
            idAnim = window.requestAnimationFrame(step);
        }
    }
    idAnim = window.requestAnimationFrame(step);
    return idAnim;
}
const startCar = async (idCar) => {
    try {
        const data = await apiStartMotor(idCar);
        const velocity = Number(data.velocity);
        const distance = Number(data.distance);
        time = distance / velocity;
        const car = document.getElementById(`img-${idCar}`);
        if (car instanceof HTMLElement) {
            const screenWidth = document.body.clientWidth;
            const positionCar = (screenWidth / 100) * 15;
            const distanceAnimation = screenWidth - positionCar;
            animation(car, distanceAnimation, time);
            const arrDrive = await apiDriveMotor(idCar);
            if (!arrDrive.success) {
                window.cancelAnimationFrame(idAnim);
            }
        }
    }
    catch (error) {
        console.error('Error starting car:', error.message);
    }
};
document.addEventListener('click', async (e) => {
    const button = e.target;
    console.log(button);
    if (button.classList.contains('car-selectors__start')) {
        idStart = Number(button.getAttribute('id'));
        console.log(idStart);
        startCar(idStart);
    }
});
